.button {
  position: relative;
  display: inline-block;
  font-size: inherit;
  margin: 0 0.25rem 1rem 0;
  padding: 0.75em 1.5em;
  border: 0;
  line-height: 1.15;
  vertical-align: baseline;
  text-decoration: none;
  text-align: center;
  transition: background 150ms ease-out, color 150ms ease-out;
  box-sizing: border-box;
  border-radius: 0.25rem;
  font-weight: 500;
  background-color: #ffd91b;
  background-image: linear-gradient(to left, #ffd91b 50%, black 50%);
  background-size: 200% 100%;
  background-position: right bottom;
  background-repeat: no-repeat;
  color: black; }
  .button:hover, .button:focus, .button:active {
    color: white;
    background-position: 0% bottom; }
  .button--xs {
    font-size: 0.75em; }
  .button--sm {
    font-size: 0.875em; }
  .button--lg {
    font-size: 1.2em; }
  .button--xl {
    font-size: 1.5em; }
  .button--block {
    display: block;
    width: 100%;
    text-align: center; }
  .button--pointy:after {
    content: '\25B6\FE0E';
    padding-left: 0.5em;
    transition: transform 200ms; }
  .button--clipped {
    border-radius: 0;
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0.75em 100%, 0 calc(100% - 0.75em)); }
  .button--caps {
    text-transform: uppercase; }
  .button--slate {
    background-image: linear-gradient(to left, #394851 50%, black 50%);
    color: white; }
    .button--slate:hover, .button--slate:focus {
      color: white; }
  .button--outline {
    color: black;
    border: 0.2em solid #ffd91b;
    padding: 0.6em 2em;
    background-image: linear-gradient(to left, white 50%, #ffd91b 50%); }
    .button--outline:hover, .button--outline:focus, .button--outline:active {
      color: black; }
  .button--outline-slate {
    color: black;
    border: 0.2em solid #394851;
    padding: 0.6em 2em;
    background-color: transparent;
    background-image: linear-gradient(to left, rgba(255, 255, 255, 0) 50%, #394851 50%); }
    .button--outline-slate:hover, .button--outline-slate:focus, .button--outline-slate:active {
      color: white; }

html {
  font-size: 100%;
  text-size-adjust: 100%; }
